import BigNumber from 'bignumber.js'
import React from 'react'
import styled from 'styled-components'
import { Box, CardBody, Flex, Heading, Text, CardRibbon } from '@pancakeswap/uikit'
import ConnectWalletButton from 'components/ConnectWalletButton'
import { useTranslation } from 'contexts/Localization'
import { BIG_ZERO } from 'utils/bigNumber'
import { getFullDisplayBalance, formatNumber, getDecimalAmount, getBalanceNumber } from 'utils/formatBalance'
import { DeserializedPool } from 'state/types'
import { PoolCategory } from 'config/constants/types'
import { TokenPairImage } from 'components/TokenImage'
import Balance from 'components/Balance'
import { StyledCard } from './StyledCard'
import CardFooter from './CardFooter'
import PoolCardHeader, { PoolCardHeaderTitle } from './PoolCardHeader'
import CardActions from './CardActions'
import ApprovalAction from './CardActions/ApprovalAction'
import StakeActions from './CardActions/StakeActions'
import HarvestActions from './CardActions/HarvestActions'

const InlineText = styled(Text)`
  display: inline;
`

interface UserInfo {
  id: string
  user: string
  amount: string
}

const PoolCard: React.FC<{ pool: DeserializedPool; account: string; userInfos: UserInfo[] }> = ({ pool, account, userInfos }) => {
  const { sousId, stakingToken, earningToken, isFinished, userData, harvest, earningTokenPrice, poolCategory, totalStaked } = pool
  const { t } = useTranslation()
  const isBnbPool = poolCategory === PoolCategory.BINANCE
  const stakedBalance = userData?.stakedBalance ? new BigNumber(userData.stakedBalance) : BIG_ZERO
  const stakingTokenBalance = userData?.stakingTokenBalance ? new BigNumber(userData.stakingTokenBalance) : BIG_ZERO
  const earnings = userData?.pendingReward ? new BigNumber(userData.pendingReward) : BIG_ZERO
  const allowance = userData?.allowance ? new BigNumber(userData.allowance) : BIG_ZERO
  const isCakePool = earningToken.symbol === 'CAKE' && stakingToken.symbol === 'CAKE'
  const accountHasStakedBalance = stakedBalance.gt(0)
  const needsApproval = !allowance.gt(0) && !isBnbPool
  const isStaked = stakedBalance.gt(0)
  const isLoading = !userData
  const tokenBalance = getBalanceNumber(stakingTokenBalance)
  const stakedTokenBalance = getBalanceNumber(stakedBalance)
  const totalStakedTokenBalance = getBalanceNumber(totalStaked, stakingToken.decimals)

  console.log("pooh, earnings = ", getBalanceNumber(earnings))

  return (
    <Flex width="100%" justifyContent="space-between" flexDirection={["column", "column", "row"]} >
      <Flex flexDirection="column" width={["100%", "100%", "60%"]}>
        <StyledCard>
          <PoolCardHeader>
            <Heading>{t('My DeltaFlip Staking')}</Heading>
          </PoolCardHeader>
          <CardBody>
            <Flex justifyContent="start" alignItems="center">
              <Flex flexDirection="column" alignItems="start" width="50%">
                <Text color="text">{t('Staked')}</Text>
                <Balance bold fontSize="20px" decimals={3} value={stakedTokenBalance} unit=" DeltaF" />
              </Flex>
              <Flex flexDirection="column" alignItems="start" width="50%">
                <Text color="text">{t('Available In Wallet')}</Text>
                <Balance bold fontSize="20px" decimals={3} value={tokenBalance} unit=" DeltaF" />
              </Flex>
            </Flex>
            <Flex mt="24px" flexDirection="column">
              {needsApproval ? (
                <ApprovalAction pool={pool} isLoading={isLoading} />
              ) : (
                <StakeActions
                  isLoading={isLoading}
                  pool={pool}
                  stakingTokenBalance={stakingTokenBalance}
                  stakedBalance={stakedBalance}
                  isBnbPool={isBnbPool}
                  isStaked={isStaked}
                />
              )}
            </Flex>
          </CardBody>
        </StyledCard>
        <StyledCard>
          <PoolCardHeader>
            <Heading>{t('Claimable')}</Heading>
          </PoolCardHeader>
          <CardBody>
            <Flex flexDirection="column">
              {harvest && (
                <HarvestActions
                  earnings={earnings}
                  earningToken={earningToken}
                  sousId={sousId}
                  earningTokenPrice={earningTokenPrice}
                  isBnbPool={isBnbPool}
                  isLoading={isLoading}
                />
              )}
            </Flex>
          </CardBody>
        </StyledCard>
      </Flex>
      <Flex flexDirection="column" width={["100%", "100%", "35%"]}>
        <StyledCard>
          <PoolCardHeader>
            <Heading>{t('Total Staked')}</Heading>
          </PoolCardHeader>
          <CardBody>
            <Balance bold fontSize="20px" decimals={3} value={totalStakedTokenBalance} unit=" DeltaF" />
          </CardBody>
        </StyledCard>
        <StyledCard>
          <PoolCardHeader>
            <Heading>{t('My Rank')}</Heading>
          </PoolCardHeader>
          <CardBody>
            {account && userInfos && userInfos.map((info, index) => {
              console.log("pooh, index = ", index)
              if (account.toLowerCase() !== info.user) {
                return null
              }

              return (
                <Flex>
                  <Flex width="50%">
                    <Text fontSize="20px" bold>{t('Rank: %rank%', {rank: index + 1})}</Text>
                  </Flex>
                  <Flex width="50%">
                    <Text fontSize="20px" bold>{t('Point: %point%', {point: getBalanceNumber(new BigNumber(info.amount))})}</Text>
                  </Flex>
                </Flex>
              )
            })}
            
          </CardBody>
        </StyledCard>
      </Flex>
    </Flex>
  )
}

export default PoolCard
