import React from 'react'
import styled, { keyframes } from 'styled-components'
import { Flex, Link } from '@pancakeswap/uikit'
import { NavLink } from 'react-router-dom'
import SideBar from '../SideBar'
import { links } from "../SideBar/config"

const MenuEntry = styled(Link)`
  width: 100%;
  padding: 15px 20px;
  font-size: 22px;
  color: white;
  font-weight: 700;
  text-align: center;
  justify-content: center;
`

interface MobileMenuProps {
  onDismiss: () => void
  visible?: boolean
}

const MobileMenu: React.FC<MobileMenuProps> = ({ onDismiss, visible }) => {
  if (visible) {
    return (
      <StyledMobileMenuWrapper>
        <StyledBackdrop onClick={onDismiss} />
        <StyledMobileMenu>
          {links.map((entry) => {
            return(
              <MenuEntry href={entry.href}>
                {entry.label}
              </MenuEntry>
            )
          })}
        </StyledMobileMenu>
      </StyledMobileMenuWrapper>
    )
  }
  return null
}

const StyledBackdrop = styled.div`
  background: linear-gradient(77deg, rgba(40,46,60,0.7) 0%, rgba(58,72,85,0.7) 100%);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
`

const StyledMobileMenuWrapper = styled.div`
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
`

const StyledMobileMenu = styled.div`
  background: linear-gradient(77deg, rgba(40,46,60,1) 0%, rgba(58,72,85,1) 100%);
  animation: 0.1s forwards ease-out;
  display: flex;  
  flex-direction: column;
  align-content: center;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 240px;
  justify-content: center;
  margin-top: -120px;
`

export default MobileMenu
