import BigNumber from 'bignumber.js'
import React, { useCallback, useState, useMemo, useEffect } from 'react'
import styled from 'styled-components'
import { formatDistanceToNowStrict } from 'date-fns'
import { Card, Text, Flex, Box, Radio, Skeleton, LinkExternal, ArrowForwardIcon, ArrowBackIcon } from '@pancakeswap/uikit'
import { formatAmount } from 'views/Info/utils/formatInfoNumbers'
import { getBscScanLink } from 'utils'
import truncateHash from 'utils/truncateHash'
import { getFullDisplayBalance, formatNumber, getDecimalAmount, getBalanceNumber } from 'utils/formatBalance'
import { Transaction, TransactionType } from 'state/info/types'
import { useTranslation } from 'contexts/Localization'
import { useTokenData, useTokenTransactions } from 'state/info/hooks'
import { PoolUpdater, ProtocolUpdater, TokenUpdater } from 'state/info/updaters'
import { PageButtons, Arrow, Break } from '../../Info/components/InfoTables/shared'

const Wrapper = styled(Card)`
  width: 100%;
  min-width: 304px;
  max-width: 540px;
`

const ResponsiveGrid = styled.div`
  display: grid;
  grid-gap: 1em;
  align-items: center;
  grid-template-columns: 0.5fr 2fr 1fr;
  padding: 10px 24px;
`

const TableLoader: React.FC = () => {
  const loadingRow = (
    <ResponsiveGrid>
      <Skeleton />
      <Skeleton />
      <Skeleton />
    </ResponsiveGrid>
  )
  return (
    <>
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
      {loadingRow}
    </>
  )
}

interface LeaderProps {
  rank: number
  wallet: string
  point: string
}

const DataRow: React.FC<LeaderProps> = ({ rank, wallet, point }) => {
  const { t } = useTranslation()

  const getEllipsisAddress = (address: string, startLength = 6, endLength = 4) => {
    return `${address.substring(0, startLength)}...${address.substring(address.length - endLength)}`
  }

  return (
    <>
      <ResponsiveGrid>
        <Text>{rank}</Text>
        <LinkExternal href={getBscScanLink(wallet, 'address')}>
          <Text>{getEllipsisAddress(wallet)}</Text>
        </LinkExternal>
        <Text>{getFullDisplayBalance(new BigNumber(point), 18, 2)}</Text>
      </ResponsiveGrid>
      <Break />
    </>
  )
}

interface UserInfo {
  id: string
  user: string
  amount: string
}

interface LeaderBoardProps {
  userInfos: UserInfo[]
}

const LeaderBoard: React.FC<LeaderBoardProps> = ({ userInfos }) => {
  const infos = userInfos
  const ITEMS_PER_PAGE = 10
  const { t } = useTranslation()
  const [page, setPage] = useState(1)
  const [maxPage, setMaxPage] = useState(1)

  const sortedUserInfos = useMemo(() => {
    return infos
      ? infos
          .slice(ITEMS_PER_PAGE * (page - 1), page * ITEMS_PER_PAGE)
      : []
  }, [infos, page, ITEMS_PER_PAGE])

  // Update maxPage based on amount of items & applied filtering
  useEffect(() => {
    if (infos) {
      if (infos.length % ITEMS_PER_PAGE === 0) {
        setMaxPage(Math.floor(infos.length / ITEMS_PER_PAGE))
      } else {
        setMaxPage(Math.floor(infos.length / ITEMS_PER_PAGE) + 1)
      }
    }
  }, [infos, ITEMS_PER_PAGE])

  return (
    <Wrapper>
      <ResponsiveGrid>
        <Text color="secondary" fontSize="16px" bold textTransform="uppercase">
          {t('Rank')}
        </Text>
        <Text color="secondary" fontSize="16px" bold textTransform="uppercase">
          {t('Wallet Address')}
        </Text>
        <Text color="secondary" fontSize="16px" bold textTransform="uppercase">
          {t('Point')}
        </Text>
      </ResponsiveGrid>
      <Break />
      {infos ? (
        <>
          {sortedUserInfos.map((info, index) => {
            if (info) {
              return (
                // eslint-disable-next-line react/no-array-index-key
                <React.Fragment key={index}>
                  <DataRow rank={index + 1} wallet={info.user} point={info.amount} />
                </React.Fragment>
              )
            }
            return null
          })}
          {sortedUserInfos.length === 0 ? (
            <Flex justifyContent="center">
              <Text>{t('No Datas')}</Text>
            </Flex>
          ) : undefined}
          <PageButtons>
            <Arrow
              onClick={() => {
                setPage(page === 1 ? page : page - 1)
              }}
            >
              <ArrowBackIcon color={page === 1 ? 'textDisabled' : 'primary'} />
            </Arrow>

            <Text>{t('Page %page% of %maxPage%', { page, maxPage })}</Text>
            <Arrow
              onClick={() => {
                setPage(page === maxPage ? page : page + 1)
              }}
            >
              <ArrowForwardIcon color={page === maxPage ? 'textDisabled' : 'primary'} />
            </Arrow>
          </PageButtons>
        </>
      ) : (
        <>
          <TableLoader />
        </>
      )}
    </Wrapper>
  )
}

export default LeaderBoard
