import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { Flex, Link } from '@pancakeswap/uikit'
import { links, socials } from "./config";

const StyledSideBarArea = styled.div`
  width: 300px;
  z-index: 1;
  color: white;
`

const LogoImage = styled.img`
  width 180px;
`

const MenuText = styled.div`
  font-size: 20px;
  font-weight: 300;
`

const MenuEntry = styled(Link)`
  width: 100%;
  padding: 15px 20px;
  font-size: 22px;
  color: white;
  font-weight: 700;
  border-top: 2px solid white;
  border-bottom: 2px solid black;
  text-align: center;
  justify-content: center;

  &:hover {
    background-color: #dd1173;
  }
`

const SubMenuEntryArea = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const MenuArea = styled.div`
  padding: 30px 0;
  display: flex;
  flex-direction: column;
`

const SocialArea = styled.div`
  position: fixed;
  bottom: 20px;
  width: 240px;
  padding: 0 10px;
  text-align: center;
`

const SocialItem = styled.a`
  width: 40px;
  font-family: 'Nunito';
`

const SocialImage = styled.img`
  width: 30px;
  margin: 0 5px;
`

const SideBar: React.FC = () => {
  const { spacing } = useContext(ThemeContext)
  
  return (
    <StyledSideBarArea>
      <Flex justifyContent="center" mt="20px">
        <Link href="/">
          <LogoImage src="/images/logo.png" alt="logo" />
        </Link>
      </Flex>
      <MenuArea>
        {links.map((entry) => {
          return(
            <MenuEntry href={entry.href}>
              {entry.label}
            </MenuEntry>
          )
        })}
      </MenuArea>
      <SocialArea>
        {socials.map((item) => {
          return (
            <SocialItem href={item.href} target="_blank">
              <SocialImage src={`images/${item.label}.png`} alt={`${item.label}`} />
            </SocialItem>
          )
        })}
      </SocialArea>
    </StyledSideBarArea>
  )
}

export default SideBar